import React from 'react';
import Image from 'react-bootstrap/Image';
import Layout from '../components/Layout';
import { Card } from 'react-bootstrap';

const ppic = {
    width: 200,
    height: 200
}

const Welcome = () => (
    <Layout>
        <div>
            <h2>Welcome to the developer portfolio for Fayyaadh Naidoo</h2>
            <Card style={{ width: '18rem' }}>
  <Card.Image variant="top" src='/static/images/Fayyaadh.jpg' roundedCircle style={ppic}/>
  <Card.Body>
    <Card.Title>Card Title</Card.Title>
    <Card.Text>
      Some quick example text to build on the card title and make up the bulk of
      the card's content.
    </Card.Text>
  </Card.Body>
</Card>
            <Image src='/static/images/Fayyaadh.jpg' roundedCircle style={ppic}></Image>
            
            <h3>Bio</h3>
            <p>I started to learn
programming during my studies as a means of picking up an extra hobby
and ended up falling in love with it. It felt like something which had
been missing in my life and my only regret was not picking it up
sooner. I then decided that this is what I wanna do for the rest of my
life as it is something that always challenges you and picks your brain
with all the problem solving that it involves. So, my next goal in life
was to pursue a career in this field. This led me to HyperionDev
where I enrolled in a Full Stack Web Development course in which I hope
to improve upon my coding knowledge and hopefully transition into a
career in Software Development once I'm done.
</p>
        </div>
        </Layout>
)
export default Welcome;